import styled from "styled-components";

export const ContainerBox = styled.div` 
  font-size: 1em;
  border: 1px solid #ced4da;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  align-items:center;
  padding-left: 1em;
  padding-right: 1em;
  padding: 6px 12px;
  height: 48px;
 
`;

 

export const Input = styled.input`
  padding: 0;
  margin: 0;
  color: #495057;
  background: transparent;
  border: none;
  outline: none;
  font-size: 16px;
  flex: 1;
  display: flex;

  ::-webkit-input-placeholder {
      font-style: normal;
  }
`;

export const InputIcon = styled.div`
  margin-right: .5em;
`
