import React,{useState} from "react"; 
import DatePicker,{ registerLocale, setDefaultLocale } from  "react-datepicker";
import { Link } from "react-router-dom";

//import moment from 'moment';

import "react-datepicker/dist/react-datepicker.css";
import './styles.css';
import "./datepicker-mob.css";

import Capa       from '~/components/Capa';
import Apartments from '~/components/Apartments';

import  history       from '~/services/history';

export default function Home() {
  
  const [startDate, setStartDate] = useState(new Date());
  const [progressBarActive, setProgressBarActive] = useState(1);
  const [search, setSearch]       = useState('');
 

  const monthsBG = ['Janeiro', 'Fevereiro', 'Março', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'];
  const daysBG = ['Dom', 'Seg', 'Ter', 'Qua', 'Qui', 'Sex', 'Sab'];

registerLocale('es', {
  localize: {
    month: n => monthsBG[n],
    day: n => daysBG[n]
  }, 
  formatLong:{} 
});


function addDays(date, days) {
     var result = new Date(date);
     result.setDate(result.getDate() + days);
     return result;
}


 
function handleSearch(e) {
     history.push('/#/apartments')
}


 

  return (
         <div  className="container">
                 <Capa active={progressBarActive} />

                 {!search &&  <div className="search-box">
                     <div className="inp-box">
                          <label>Data de entrada</label>
                          <DatePicker
                              locale="es"
                              minDate={new Date()}
                              dateFormat="dd/MM/yyyy"
                              showPopperArrow={false}
                              selected={startDate}
                              onChange={date => setStartDate(date)}
                              onFocus={e => e.target.blur()}
                               
                         />
                     </div>
                     <div className="inp-box">
                          <label>Data de saída</label>
                          <DatePicker
                              locale="es"
                              minDate={new Date()}
                              dateFormat="dd/MM/yyyy"
                              showPopperArrow={false}
                              selected={addDays(startDate,1)}
                              onChange={date => setStartDate(date)}
                              onFocus={e => e.target.blur()}
                         />                           
                     </div> 

                     <div className="inp-box">
                          <label>Adultos</label>
                          <select>
                               <option>1</option>
                               <option>2</option>
                               <option>3</option>
                               <option>4</option>
                          </select>
                     </div> 

                     <div className="inp-box">
                          <label>Crianças</label>
                          <select>
                               <option>0</option>
                               <option>1</option>
                               <option>2</option>
                               <option>3</option>
                          </select>
                     </div>    
                     <div>
                         <Link to="/apartments"> 
                             <button className="btn btn-default availability-action"   >Pesquisar</button>
                         </Link>
                     </div>  
                                                                          
  </div> }

         </div> 
    );
}

